.main-login-component {
  height: 100%;
  min-height: 100vh;
}


#Warning{
  h5{
    color: #C82F2F;
text-align: center;
font-family: Urbanist;
font-size: 32px;
font-style: normal;
font-weight: 600;
line-height: 51.189px; /* 159.965% */
letter-spacing: 0.32px;
  }
  h6{
    color: #000;
text-align: center;
font-family: Urbanist;
font-size: 22px;
font-style: normal;
font-weight: 500;
line-height: 31px; /* 140.909% */
letter-spacing: 0.32px;
  }
  p{
    color: rgba(0, 0, 0, 0.61);
text-align: center;
font-family: Urbanist;
font-size: 16px;
font-style: normal;
